"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"

interface Product {
  id: number
  name: string
  price: number
  originalPrice: number
  image: string
  description: string
  category: string
  discountPercentage: number
}

export default function Offers() {
  const [products] = useState<Product[]>([
    {
      id: 1,
      name: "Vestido Casual",
      price: 89.99,
      originalPrice: 129.99,
      image: "https://images.pexels.com/photos/994234/pexels-photo-994234.jpeg",
      description: "Vestido casual perfecto para el día a día",
      category: "Vestidos",
      discountPercentage: 30
    },
    {
      id: 2,
      name: "Blusa de Verano",
      price: 49.99,
      originalPrice: 79.99,
      image: "https://images.pexels.com/photos/1007018/pexels-photo-1007018.jpeg",
      description: "Blusa fresca y elegante para el verano",
      category: "Blusas",
      discountPercentage: 37
    },
    {
      id: 3,
      name: "Zapatillas Running",
      price: 119.99,
      originalPrice: 199.99,
      image: "https://images.pexels.com/photos/2529148/pexels-photo-2529148.jpeg",
      description: "Zapatillas deportivas con máxima comodidad",
      category: "Zapatillas",
      discountPercentage: 40
    }
  ])

  const handleWhatsAppQuote = (product: Product) => {
    const message = `Hola, me interesa esta oferta:\n\n` +
      `Producto: ${product.name}\n` +
      `Precio Original: S/. ${product.originalPrice}\n` +
      `Precio Oferta: S/. ${product.price}\n` +
      `Descuento: ${product.discountPercentage}%\n` +
      `Categoría: ${product.category}\n\n` +
      `¿Podrían brindarme más información?`
    const whatsappUrl = `https://wa.me/519XXXXXXXX?text=${encodeURIComponent(message)}`
    window.open(whatsappUrl, "_blank")
  }

  return (
    <div className="min-h-screen bg-gray-50 py-12">
      {/* Header Section */}
      <div className="container mx-auto px-4 mb-12">
        <div className="text-center">
          <h1 className="text-4xl font-bold mb-4">Ofertas Especiales</h1>
          <p className="text-gray-600 max-w-2xl mx-auto">
            Aprovecha nuestras increíbles ofertas en productos seleccionados. 
            ¡No te pierdas estas oportunidades únicas!
          </p>
        </div>
      </div>

      {/* Products Grid */}
      <div className="container mx-auto px-4">
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
          {products.map((product) => (
            <Card 
              key={product.id} 
              className="overflow-hidden group hover:shadow-xl transition-shadow duration-300"
            >
              <div className="relative">
                {/* Discount Label */}
                <div className="absolute top-4 right-4 z-10">
                  <span className="bg-pink-600 text-white px-3 py-1 rounded-full text-sm font-medium">
                    -{product.discountPercentage}%
                  </span>
                </div>

                {/* Product Image */}
                <div className="relative h-[300px] overflow-hidden">
                  <div 
                    className="absolute inset-0 bg-cover bg-center transform group-hover:scale-110 transition-transform duration-500"
                    style={{
                      backgroundImage: `url(${product.image})`
                    }}
                  />
                  <div className="absolute inset-0 bg-gradient-to-t from-black/60 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300" />
                  <div className="absolute bottom-0 left-0 right-0 p-4 text-white transform translate-y-full group-hover:translate-y-0 transition-transform duration-300">
                    <p className="text-sm">{product.description}</p>
                  </div>
                </div>

                {/* Product Info */}
                <div className="p-6">
                  <div className="flex justify-between items-start mb-2">
                    <h3 className="text-xl font-semibold">{product.name}</h3>
                    <span className="text-sm text-gray-500">{product.category}</span>
                  </div>
                  <div className="flex items-center justify-between mt-4">
                    <div className="space-y-1">
                      <span className="text-2xl font-bold text-pink-600 block">
                        S/. {product.price}
                      </span>
                      <span className="text-sm text-gray-500 line-through block">
                        S/. {product.originalPrice}
                      </span>
                    </div>
                    <Button 
                      onClick={() => handleWhatsAppQuote(product)}
                      className="bg-pink-600 hover:bg-pink-700"
                    >
                      Cotizar
                    </Button>
                  </div>
                </div>
              </div>
            </Card>
          ))}
        </div>
      </div>

      {/* Promotion Banner */}
      <div className="container mx-auto px-4 mt-16">
        <div className="bg-gradient-to-r from-pink-600 to-pink-700 rounded-lg p-8 text-white text-center">
          <h2 className="text-2xl font-bold mb-4">¡Ofertas por Tiempo Limitado!</h2>
          <p className="mb-6">
            Aprovecha estos descuentos especiales antes que se agoten. 
            Precios increíbles en productos seleccionados.
          </p>
          <Button 
            variant="outline" 
            className="bg-white text-pink-600 hover:bg-gray-100"
            onClick={() => document.documentElement.scrollTo({ top: 0, behavior: 'smooth' })}
          >
            Ver Todas las Ofertas
          </Button>
        </div>
      </div>
    </div>
  )
}
